

.popup {
    height: 100vh;
    width: 200vh;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    opacity: 0;
    pointer-events: none;
}

.popupContent {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    transform: scale(0.5);
    transition: 0.4s all;
    width: 50vw;
    background-color: #efefef;

}

.popupContent.active{
    transform: scale(1);
}

.popup.active {
    transform: scale(1);
    opacity: 1;
    pointer-events: all;
    overflow-y: auto;
}
